import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { GroceriesServiceProvider } from '../groceries-service/groceries-service';

/*
  Generated class for the InputDialogServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class InputDialogServiceProvider {

  constructor(public http: HttpClient, public dataService: GroceriesServiceProvider, public alertCtrl: AlertController) {
    console.log('Hello InputDialogServiceProvider Provider');
  }

  showAddItemPrompt() {
    const prompt = this.alertCtrl.create({
      title: "Add new item",
      message: "Enter a new item for the list",
      inputs: [
        {
          name: "name",
          placeholder: 'Name'
        },
        {
          name: "section",
          placeholder: 'Section'
        },
        {
          name: "quantity",
          placeholder: 'Quantity'
        }
      ],
      buttons: [
        {
          text: "Cancel",
          handler: data => {
            console.log("Canceled");
          }
        },
        {
          text: "Save",
          handler: item => {
            console.log("Saved clicked", item);
            this.dataService.addItem(item);
          }
        }
      ]
    });
    prompt.present();
  }

  showEditItemPrompt(item, index) {
    const prompt = this.alertCtrl.create({
      title: "Edit item",
      message: "Edit item in the list",
      inputs: [
        {
          name: "name",
          placeholder: 'Name',
          value: item.name
        },
        {
          name: "section",
          placeholder: 'Section',
          value: item.section
        },
        {
          name: "quantity",
          placeholder: 'Quantity',
          value: item.quantity
        }
      ],
      buttons: [
        {
          text: "Cancel",
          handler: data => {
            console.log("Canceled");
          }
        },
        {
          text: "Save",
          handler: item => {
            console.log("Saved clicked", item);
            this.dataService.editItem(item, index);
          }
        }
      ]
    });
    prompt.present();
  }

}



